@import url(../fontawesome/css/all.css);
:root {
  --font-size-initial: 12px;
  --font-size-lv2: 14px;
  --font-size-lv3: 16px;
  --font-size-lv4: 18px;
  --spacing-site: 2rem;
  --color-text-main: #000; }

/*
* Whitespace Reset */
* {
  padding: 0;
  margin: 0; }

/*
* Box-sizing fix */
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

@-webkit-keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes navtogglein {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -2rem);
    transform: translate(0, -2rem); }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.3rem);
    transform: translate(0, -0.3rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes jump {
  0% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); }
  50% {
    -webkit-transform: translate(0, -0.3rem);
    transform: translate(0, -0.3rem); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@-webkit-keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@keyframes navitemin {
  from { }
  to {
    opacity: 1;
    -webkit-transform: scale(1) translate(0, 0) rotate(0deg);
    transform: scale(1) translate(0, 0) rotate(0deg); } }

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  25% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  50% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  75% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

@keyframes shake {
  0% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  25% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  50% {
    -webkit-transform: translate(-2rem, 0);
    transform: translate(-2rem, 0); }
  75% {
    -webkit-transform: translate(2rem, 0);
    transform: translate(2rem, 0); }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0); } }

html {
  font: var(--font-size-initial, 12px)/1.5 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--font-size-lv2, 14px); } }

@media screen and (min-width: 700px) {
  html {
    font-size: var(--font-size-lv3, 16px); } }

@media screen and (min-width: 1600px) {
  html {
    font-size: var(--font-size-lv3, 18px); } }

body, table, th, td, form, input, textarea, option, select {
  font: inherit; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  margin-bottom: 0.5em; }

h1 {
  font-size: 2.441em; }

h2 {
  font-size: 1.953em; }

h3 {
  font-size: 1.563em; }

h4 {
  font-size: 1.25em; }

h5 {
  font-size: 1em; }

h6 {
  font-size: 1em;
  font-weight: bold; }

p, ul, ol {
  padding-bottom: 1.5em; }

ul, ol {
  padding-left: 2em; }

li {
  list-style-position: inside; }

form {
  -webkit-animation: shake 0.5s 1 ease-in-out forwards;
  animation: shake 0.5s 1 ease-in-out forwards; }

fieldset,
fieldset legend {
  border: 2px solid #CCC;
  border-radius: 0.3em;
  -webkit-box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 2rem 0 rgba(0, 0, 0, 0.1); }

fieldset {
  background: #FFF;
  padding: 3.5vh 4.5vw 3vh 4.5vw; }

fieldset legend {
  display: inline-block;
  background: #FFF;
  padding: 0.6em 2.2em;
  font-weight: bold; }

label {
  font-weight: bold;
  display: block; }

input[type="text"],
input[type="password"] {
  width: 20rem;
  display: block;
  border: 2px solid #CCC;
  padding: 0.4em 1em;
  border-radius: 0.4em;
  background: #EEE;
  outline: none;
  -webkit-transition: background 0.3s ease, border-color 0.3s ease;
  transition: background 0.3s ease, border-color 0.3s ease; }

input[type="text"]:focus,
input[type="password"]:focus {
  background: #FFF;
  border-color: #333; }

input[type="submit"] {
  display: block;
  border: 2px solid #000;
  background: #333;
  color: #FFF;
  padding: 0.6em 2.2em;
  border-radius: 0.4em;
  font-weight: bold;
  cursor: pointer; }

input[type="submit"]:hover {
  background: #060;
  -webkit-transition: background 0.3s ease, border-color 0.3s ease;
  transition: background 0.3s ease, border-color 0.3s ease; }

body {
  color: var(--color-text-main, red); }

.site {
  padding: var(--spacing-site, 2em); }

.site-content {
  max-width: 60rem;
  margin: 0 auto; }

body.login {
  height: 100vh;
  overflow: hidden;
  padding-top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

body.login .site {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  background: #EEE;
  border: 4rem solid #FFF;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between; }

body.login .site-header {
  display: none; }

.navigation {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9); }

.navigation ul {
  padding: 0;
  margin: 0; }

.navigation li {
  display: block;
  list-style: none outside;
  padding: 1em 0;
  -webkit-animation: navitemin 0.3s 1 ease forwards;
  animation: navitemin 0.3s 1 ease forwards;
  opacity: 0;
  -webkit-transform: scale(0.1) translate(0, 3rem) rotate(720deg);
  transform: scale(0.1) translate(0, 3rem) rotate(720deg); }

.navigation li:nth-child(1) {
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s; }

.navigation li:nth-child(2) {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

.navigation li:nth-child(3) {
  -webkit-animation-delay: 0.45s;
  animation-delay: 0.45s; }

.navigation a {
  display: block;
  padding: 1.5em 2.5em;
  font-size: 1.5em;
  background: white;
  text-decoration: none;
  color: #444;
  border-radius: 0.5rem;
  -webkit-box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 1.5rem 0.1rem rgba(0, 0, 0, 0.1); }

.navigation a:before {
  margin-right: 1em; }

#nav-main-toggle {
  display: none; }

.nav-toggle {
  position: fixed;
  top: var(--spacing-site, 2rem);
  left: var(--spacing-site, 2rem);
  z-index: 1000;
  font-size: 2rem;
  padding: 0 0.5rem;
  -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
  animation: navtogglein 0.5s 1 ease-in-out forwards; }

#nav-toggle-right {
  left: inherit;
  right: var(--spacing-site, 2rem); }

.nav-toggle span {
  display: none; }

.nav-toggle:before {
  content: '\f0c9'; }

.nav-toggle:before {
  margin-right: 1rem; }

#nav-main-toggle:checked + .site-header .navigation {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 1000; }

#nav-main-toggle:checked + .site-header .nav-toggle:before {
  content: '\f00d'; }

#nav-main-toggle:checked + .site-header .nav-toggle {
  z-index: 1010;
  -webkit-animation: jump 0.5s 1 ease-in-out forwards;
  animation: jump 0.5s 1 ease-in-out forwards; }

.listing {
  padding: 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
