.navigation {
    display: none;
    position: fixed;
    top: 0; left: 0; right: 0; bottom: 0;
    align-items: center;
    justify-content: center;
    background: rgba(255,255,255,0.9);
}

.navigation ul {
    padding: 0;
    margin: 0;
}

.navigation li {
    display: block;
    list-style: none outside;
    padding: 1em 0;
    animation: navitemin 0.3s 1 ease forwards;
    opacity: 0;
    transform: scale(0.1) translate(0, 3rem) rotate(720deg);
}

.navigation li:nth-child(1) { animation-delay: 0.15s; }
.navigation li:nth-child(2) { animation-delay: 0.3s; }
.navigation li:nth-child(3) { animation-delay: 0.45s; }

.navigation a {
    display: block;
    padding: 1.5em 2.5em;
    font-size: 1.5em;
    background: white;
    text-decoration: none;
    color: #444;
    border-radius: 0.5rem;
    box-shadow: 0 0 1.5rem 0.1rem rgba(0,0,0,0.1);
}

.navigation a:before {
    margin-right: 1em;
}

#nav-main-toggle {
    display: none;
}

.nav-toggle {
    position: fixed;
    top: var(--spacing-site, 2rem);
    left: var(--spacing-site, 2rem);
    z-index: 1000;
    font-size: 2rem;
    padding: 0 0.5rem;
    animation: navtogglein 0.5s 1 ease-in-out forwards;
}

#nav-toggle-left {

}

#nav-toggle-right {
    left: inherit;
    right: var(--spacing-site, 2rem);
}

.nav-toggle span {
    display: none;
}

.nav-toggle:before {
    content: '\f0c9';
}

.nav-toggle:before {
    margin-right: 1rem;
}

#nav-main-toggle:checked + .site-header .navigation {
    display: flex;
    z-index: 1000;
}

#nav-main-toggle:checked + .site-header .nav-toggle:before {
    content: '\f00d';
}

#nav-main-toggle:checked + .site-header .nav-toggle {
    z-index: 1010;
    animation: jump 0.5s 1 ease-in-out forwards;
}
